.dialogs{
    background-color: white;
    display: flex;
    color: black;

}
.dialogs_item a{
    color: black;
    display: block;
    text-decoration: none;
    background-color: #0c90b1;
    width: 100%;
    height: 50px;
    transition: 400ms;
    display: flex;
    justify-content: center;
    align-items: center;
}
.dialogs_item a:hover{
    background-color: aqua;
}
.dialogs_item{
    width: 30%;
}
.messages{
    padding: 0 10px;
    border-radius: 10px;
    width: 70%;
    box-shadow: #8eafb6 0px 0px 10px 2px inset;
    overflow: auto;
    height: 500px;
}
.dialogs_item a.active{
    color:gold;
}