.nav{
    grid-area: n;
    background: #07B568;

}
.item{
    justify-self: center;
    width: 100%;
}
.item a{
    color: white;
    display: block;
    text-decoration: none;
    background-color: #07b568;
    width: 100%;
    height: 50px;
    transition: 400ms;
    display: flex;
    justify-content: center;
    align-items: center;
}
.item a:hover{
    background-color: #10936a;
}
.item a.active{
    color: gold;
}